body {
  background-color: #D6DCE8;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #D6DCE8;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.error {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: $blue;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.app {
  border: 4px solid $blue;
  color: $blue;
  width: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  &__city {
    font-size: 40px;
    padding: 15px;
    border-bottom: 4px solid #516490;
  }
  &__latlong {
    display: flex;
    width: 100%;
  }

}

.latlong {
  border-bottom: 4px solid $blue;
  &__lat, &__long {
    width: 50%;
    font-size: 20px;
    padding: 10px;
  }
  &__lat {
    border-right: 2px solid $blue;
  }
  &__long {
    border-left: 2px solid $blue;
  }
}

.date {
  padding: 10px;
  border-bottom: 4px solid $blue;
  &__day, &__hour {
    font-size: 30px;
  }
}

.weather {
  padding: 10px;
  border-bottom: 4px solid $blue;
}
.details {
  padding: 10px;
  display: flex;
  &__humadity, &__wind {
    width: 50%;
  }
}

.temp-toogle {
  width: 100%;
  height: 30px;
}
.loader {
    border: 10px solid #f3f3f3; /* Light grey */
    border-top: 10px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 70px;
    height: 70px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@media screen and (max-width: 500px) {
  .app {
    width: 100%;
    margin-left: 10px;
    margin-right: 10px;
  }
}
